import React from "react";
import Context from "../../Context";
import {injectIntl} from "react-intl";
import key from "../../util/key";
import {
  PlusOutlined,
  QuestionCircleOutlined,
  SearchOutlined,
  UnorderedListOutlined,
} from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import {Button, Modal, Avatar} from "antd";
import {Link} from "react-router-dom";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import {Helmet} from "react-helmet";
import Flex from "../../util/Flex";
import {useSelector} from "react-redux";

//import InvoiceTransactions from "./InvoiceTransactions";

class Clients extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],

      showTransactions: false,
      currentItem: {},
    };
  }

  m = (key, prefix = "client.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  showTransactions = (currentItem) => {
    this.setState({currentItem, showTransactions: true});
  };

  closeTransactions = () => {
    this.setState({showTransactions: false});
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          text: true,
          title: this.m("name"),
          dataIndex: "code",
          width: "20%",
          render: (value, item) => (
            <>
              <div className="gx-flex-row gx-align-items-center gx-flex-nowrap">
                {item.picture ? <Avatar
                  src={item.picture}
                  className="gx-size-40 gx-pointer gx-mr-3"
                  alt=""
                /> : <div className="gx-size-40 gx-mr-3"></div>}
                <div className="gx-flex-column gx-align-items-left">
                  <Link to={`/crm/clients/${item.id}`}>
                    <div>{item.code}</div>
                  </Link>

                  {item.fullname && (
                    <div className="gx-font-weight-light gx-text-small gx-text-muted">
                      {item.fullname}
                    </div>
                  )}
                </div>
              </div>
            </>
          ),
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("typename"),
          dataIndex: "typename",
          sorter: true,
          width: "15%",
          render: (value, item) => (
            <>
              <div>{item.typename}</div>

              {item.typedescription && (
                <div className="gx-font-weight-light gx-text-small gx-text-muted">
                  {item.typedescription}
                </div>
              )}

            </>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          width: "5%",
          title: this.m("phone"),
          dataIndex: "phone",
          sorter: true,
          render: (value, item) => <>{value ? `${value.mobile}` : "No"}</>,


        },
        {
          text: true,
          width: "5%",
          title: this.m("phoneverified"),
          dataIndex: "phoneverified",
          sorter: true,
          render: (value, item) => <>{value ? `${value}` : "No"}</>,
        },
        {
          text: true,
          width: "5%",
          title: this.m("email"),
          dataIndex: "email",
          sorter: true,
          render: (value, item) => <>{value ? `${value}` : "No"}</>,
        },
        {
          text: true,
          width: "5%",
          title: this.m("emailverified"),
          dataIndex: "emailverified",
          sorter: true,
          render: (value, item) => <>{value ? "Yes" : "No"}</>,
        },
        {
          width: "5%",
          title: this.m("created"),
          dataIndex: "created",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br/>
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          width: "5%",
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          render: (value, item) => (
            <div className="gx-text-right">
              <Statelabel onFinish={this.onMethodFinish} {...item} />
            </div>
          ),
        },
      ],
    });
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>{this.m("client")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">{this.m("client")}</h2>
        <Modal
          open={this.state.showTransactions}
          footer={null}
          onCancel={this.closeTransactions}
        >
        </Modal>
        <DataTable
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/client/list"}
        />
      </div>
    );
  }
}

export default injectIntl(Clients);
